$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.choose-page {
  padding: transformToVW(45px);

  &__new-btn-cont {
    margin-bottom: transformToVW(45px);
  }

  &__new-btn {
    width: transformToVW(300px);
    height: transformToVW(55px);

    border: transformToVW(2px) solid $main-color;
    background: none;
    outline: none;
    cursor: pointer;

    font: inherit;
    color: inherit;

    display: flex;
    justify-content: center;
    align-items: center;

    span:first-child {
      margin-right: transformToVW(15px);
    }

    span:last-child {
      width: transformToVW(16px);
      height: transformToVW(18px);

      background-image: url(../../../../img/utils-icons/plus-icon-white.svg);
      background-size: 100% 100%;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__header {
    margin-bottom: transformToVW(20px);
  }

  &__project-list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: transformToVW(25px);

    &-item {
      &-btn {
        padding: transformToVW(10px) transformToVW(15px);

        background: none;
        border: none;
        outline: none;
        cursor: pointer;

        background-color: transparent;
        transition: background-color ease-in-out 400ms;

        font: inherit;
        color: inherit;

        display: flex;
        align-items: center;
        justify-content: flex-start;

        width: 100%;

        &:hover {
          background-color: rgba($color: $select-color, $alpha: 0.2);
        }
      }

      &-point {
        padding: transformToVW(5px);

        margin-right: transformToVW(25px);

        background-color: $select-color;
      }
    }
  }
}