$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.node-settings {
  grid-column: 3/4;
  grid-row: 2/4;

  overflow-y: scroll;

  background: #313438;


  &__section {
    max-height: transformToVW(45px);
    overflow: hidden;
    border-bottom: transformToVW(1px) solid #202225;

    &-open-btn {
      display: block;
      height: transformToVW(45px);
      width: 100%;

      display: flex;
      justify-content: space-between;
      align-items: center;

      padding: 0 transformToVW(15px);

      border: none;
      outline: none;
      background: none;

      cursor: pointer;

      font: inherit;
      color: $main-color;

      img {
        width: transformToVW(12px);
        height: transformToVW(12px);

        object-fit: contain;

        transform: rotate(-90deg);
        transition: transform ease-in-out 400ms;
      }
    }
  }

  &__section_opened {
    max-height: unset;
  }

  &__section_opened > &__section-open-btn img {
    transform: rotate(0deg);
  }

  &__buttons-cont {
    display: grid;
    grid-template-columns: 1fr;
    gap: transformToVW(15px);
    padding: transformToVW(20px) transformToVW(35px);

    button {
      background: none;
      border: none;
      outline: none;

      cursor: pointer;

      font: inherit;
      color: inherit;

      height: transformToVW(35px);

    }
  }

  &__text-cont {
    padding: transformToVW(15px);
    border-bottom: transformToVW(1px) solid #202225;

    textarea {
      width: 100%;
      height: 60vh;

      font: inherit;
      color: inherit;

      background: #2D2F33;
      border: transformToVW(1px) solid #575C66;
      outline: none;

      resize:vertical;

      padding: transformToVW(10px);
    }
  }

  &__confirm-btn {
    background-color: $select-color !important;
  }

  &__delete-btn {
    border: transformToVW(1px) solid $main-color !important;
  }
}