$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.tool-panel {
  grid-column: 1/4;
  grid-row: 1/2;

  background-color: #313438;
  border-bottom: transformToVW(1px) solid #202225;

  display: flex;
  justify-content: space-between;
  align-items: stretch;

  padding: 0 transformToVW(15px);

  &__tools {
    position: relative;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    height: 100%;

    &-item {
      width: transformToVW(48px);
      height: transformToVW(48px);

      display: flex;
      align-items: center;
      justify-content: center;

      background: none;
      outline: none;
      border: none;

      cursor: pointer;

      margin-right: transformToVW(10px);

      background-color: transparent;
      transition: background-color ease-in-out 400ms;

      &:hover {
        background-color: rgba($color: $select-color, $alpha: 0.4);
      }

      &-pic {
        width: transformToVW(18px);
        height: transformToVW(18px);
        object-fit: contain;
      }

      &-drop {
        margin-left: transformToVW(5px);
        width: transformToVW(10px);
        height: transformToVW(10px);

        background-image: url(./../../../../../img/utils-icons/arrow-drop-icon-white.svg);
        background-position: 0 0;
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }
    }

    &-item_selected {
      background-color: $select-color;

      &:hover {
        background-color: $select-color;
      }
    }

    &-sub-list {
      position: absolute;
      top: 110%;
      left: transformToVW(24px);
      background-color: #202225;

      opacity: 0;
      pointer-events: none;
      transition: opacity ease-in-out 400ms;

      z-index: 10;
    }

    &-sub-list_opened {
      opacity: 1;
      pointer-events: unset;
    }    

    &-sub-item {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      width: 100%;

      background: none;
      outline: none;
      border: none;

      font: inherit;
      color: inherit;

      cursor: pointer;

      

      padding: transformToVW(10px) transformToVW(15px);

      background-color: transparent;
      transition: background-color ease-in-out 400ms;

      &:hover {
        background-color: rgba($color: $select-color, $alpha: 0.4);
      }

      &-pic {
        width: transformToVW(18px);
        height: transformToVW(18px);
        object-fit: contain;
        margin-right: transformToVW(10px);
      }
    }

    &-sub-item_selected {
      background-color: $select-color;

      &:hover {
        background-color: $select-color;
      }
    }

    @for $i from 1 through 10 {
      &-sub-list_#{$i} {
        left: transformToVW(24px + 58px * $i - 58px);
      }
    }
  }

  &__project {
    position: relative;

    display: flex;
    justify-content: flex-end;
    align-items: center;

    &-btn {
      height: transformToVW(35px);

      background: none;
      border: none;
      outline: none;
      border-radius: 0;

      cursor: pointer;

      background-color: $select-color;

      display: flex;
      justify-content: center;
      align-items: center;

      font: inherit;
      color: inherit;

      padding: 0 transformToVW(15px);
      margin-left: transformToVW(15px);

      img {
        width: transformToVW(17px);
        height: transformToVW(17px);
        object-fit: contain;

        margin-left: transformToVW(10px);
      }
    }
  }
}