@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600;700&display=swap');

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    color: #D8D8D8;
    background: #2A2C2F;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}
