$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.render-area {
  grid-column: 2/3;
  grid-row: 2/4;

  padding: transformToVW(15px);

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: transformToVW(40px) 1fr;
  gap: transformToVW(15px);

  &__reload-btn {
    height: 100%;

    cursor: pointer;

    border: none;
    background: none;
    outline: none;

    font: inherit;
    color: inherit;

    padding: 0 transformToVW(15px);

    background-color: #313438;
    transition: background-color ease-in-out 400ms;

    &:hover {
      background-color: rgba($color: $select-color, $alpha: 0.4);
      transition: background-color ease-in-out 400ms;
    }

    &:active {
      background-color: rgba($color: $select-color, $alpha: 1);
      transition: background-color ease-in-out 0ms;
    }
  }

  &__cont {
    iframe {
      width: 100%;
      height: 100%;

      background-color: #FFFFFF;
      border: none;
    }
  }
}