$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.create-page {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;

  &__cont {
    width: transformToVW(370px);
    padding: transformToVW(25px);

    background: #313438;
  }

  &__name-input-cont {
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: transformToVW(10px);

    align-items: center;

    margin-bottom: transformToVW(25px);
  }

  &__name-input-label {
    cursor: pointer;
  }

  &__name-input {
    padding: transformToVW(5px) transformToVW(15px);

    border: none;
    outline: none;
    background: #2D2F33;
    border-radius: 0;

    border-bottom: transformToVW(2px) solid #575C66;

    font: inherit;
    color: inherit;

    width: 100%;
  }

  &__create-btn {
    display: block;
    width: transformToVW(240px);
    height: transformToVW(45px);
    margin: 0 auto;

    background: $select-color;
    border: none;
    outline: none;

    cursor: pointer;
    
    font: inherit;
    color: inherit;
  }
}