$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.node-tree {
  grid-column: 1/2;
  grid-row: 3/4;

  border-top: transformToVW(2px) solid #202225;
  background: #313438;

  overflow: scroll;

  &__cont {
    position: relative;
    min-width: 100%;
    max-width: unset;
    min-height: transformToVW(20px);
  }

  &__item {
    position: relative;
    display: block;
    width: 100%;

    &-btn,
    &-add-btn {
      display: flex;
      justify-content: flex-start;
      align-items: center;

      text-align: left;
      width: 100%;
      padding: transformToVW(10px) transformToVW(15px);

      background: none;
      border: none;
      outline: none;

      font: inherit;
      color: inherit;

      cursor: pointer;

      background-color: transparent;

      &:hover {
        // border: transformToVW(1px) solid $select-color;
        background-color: rgba($color: $select-color, $alpha: 0.3);
      }
    }

    &-add-btn {
      justify-content: center;
    }

    &-btn_active {
      background-color: $select-color;

      &:hover {
        background-color: $select-color;
        // border: none;
      }
    }

    &-pic {
      width: transformToVW(17px);
      height: transformToVW(17px);

      margin-right: transformToVW(10px);

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    &-show-tree-btn {
      width: transformToVW(12px);
      height: transformToVW(12px);

      margin-right: transformToVW(10px);

      transform: rotate(-90deg);

      background: none;
      outline: none;
      border: none;

      cursor: pointer;

      background-image: url('../../../../../img/utils-icons/arrow-drop-icon-white.svg');
      background-position: 0 0;
      background-size: 100% 100%;
    }
  }

  &__item_opened > &__item-btn > &__item-show-tree-btn {
    transform: rotate(0deg);
  }

  &__item > &__cont {
    display: none;
    padding-left: transformToVW(15px);

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: transformToVW(15px + 6px);
      width: transformToVW(1px);
      height: 100%;

      background-color: $main-color;
    }
  }

  &__item > &__cont > &__item > &__item-btn::after {
    content: "";
    position: absolute;
    top: calc(50% - transformToVW(0.5px));
    left: transformToVW(6px);
    width: transformToVW(5px);
    height: transformToVW(1px);

    background-color: $main-color;
  }

  &__item_opened > &__cont {
    display: block;
  }
}