.dropdown {
  position: relative;

  &:hover ul {
      opacity: 1;
      visibility: visible;
      transform: translate(-25%, 0%);
      pointer-events: all;
  }

  // .dropdown ul
  ul {
      padding-top: 10px;
      pointer-events: none;
      z-index: 1;
      transition: all 0.3s ease 0s;
      top: 100%;
      min-width: 200px;
      background-color: #202225;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      transform: translate(-25%, 10%);

      li {
          padding: 10px;
          transition: all 0.3s ease 0s;
      }

      li:hover {
          background-color: #FF005A;
      }
  }

  // .dropdown span
  & span {
      padding: 10px;
  }

  // .dropdown img
  &__img img {
      vertical-align: middle;
      margin-left: 6px;
      transform: scale(1.5);
  }

  div {
      color: #D8D8D8;
  }

  a {
      display: flex;
      gap: 12px;
  }

  &__text {
      color: white;
  }

  &__hover {
      position: relative;
      margin-left: -8px;
  }

  &__hover::before {
      content: "";
      position: absolute;
      left: 130%;
      top: 50%;
      width: 6px;
      height: 1px;
      background-color: #fff;
      transform: rotate(45deg);
  }

  &__hover::after {
      content: "";
      position: absolute;
      left: calc(130% + 4px);
      top: 50%;
      width: 6px;
      background-color: #fff;
      height: 1px;
      transform: rotate(-45deg);
  }
}

.dropdown-menu {
  position: relative;

  &:hover ul {
      background-color: #333;
      opacity: 1;
      visibility: visible;
      transform: translate(-25%, 0%);
  }

  // .dropdown ul
  ul {
      z-index: 1;
      transition: all 0.3s ease 0s;
      top: calc(100% + 10px);
      left: 60px;
      min-width: 200px;
      background-color: #333;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      transform: translate(-25%, 10%);

      &>li:not(:last-child) {
          margin-bottom: 10px;
      }

      li {
          padding: 10px;
      }

      li:hover {
          background-color: #FF005A;
      }
  }

  // .dropdown span
  & span {
      padding: 10px;
  }

  // .dropdown img
  & img {
      vertical-align: middle;
      margin-left: 6px;
      transform: scale(1.5);
  }

  div {
      color: #636363;
  }
}

main {
  .dropdown {
      ul {
          top: 100%;
      }
  }
}
