$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}


.project-page {
  display: grid;
  grid-template-columns: transformToVW(250px) 1fr transformToVW(270px);
  grid-template-rows: transformToVW(50px) transformToVW(45px) 1fr;

  width: 100%;
  height: 100%;
}