$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.class-name-settings {
  display: grid;
  grid-template-columns: 1fr;
  gap: transformToVW(20px);
  padding: transformToVW(15px);

  &__item {
    display: grid;
    grid-template-columns: 1fr transformToVW(12px);
    align-items: center;
    gap: transformToVW(10px);
    padding: 0 transformToVW(10px);
    height: transformToVW(40px);

    border: transformToVW(1px) solid #575C66;
    background: #2D2F33;

    input {
      font: inherit;
      color: inherit;

      background: none;
      border: none;
      outline: none;
      border-radius: 0;

      text-align: left;
    }

    &-remove-btn {
      background: none;
      outline: none;
      border: none;

      cursor: pointer;

      height: transformToVW(12px);

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }

  &__add-btn {
    display: grid;
    grid-template-columns: 1fr transformToVW(12px);
    align-items: center;
    gap: transformToVW(10px);
    height: transformToVW(40px);
    padding: 0 transformToVW(10px);

    border: none;
    outline: none;
    background: $select-color;
    cursor: pointer;

    font: inherit;
    color: inherit;

    text-align: left;

    img {
      display: block;
      width: transformToVW(12px);
      height: transformToVW(12px);
      object-fit: contain;
    }
  }
}