$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}


.bar {
  width: 100%;

  display: grid;
  grid-template-columns: transformToVW(100px) 1fr transformToVW(100px);

  padding: transformToVW(5px) transformToVW(15px);

  background: #202225;

  &__logo {
    font-size: transformToVW(12px);

    span {
      color: $select-color;
    }
  }

  &__exit-btn {
    width: transformToVW(10px);
    height: transformToVW(10px);

    background: none;
    outline: none;
    border: none;
    cursor: pointer;

    background-image: url(../../../../img/utils-icons/exit-icon-white.svg);
    background-size: 100% 100%;
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  &__logo-cont,
  &__exit-btn-cont,
  &__center-cont {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }


  &__exit-btn-cont {
    justify-content: flex-end;
  }

  &__center-cont {
    justify-content: center;
  }
  
}