$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.attribute-settings {
  display: grid;
  grid-template-columns: 1fr;
  gap: transformToVW(20px);
  padding: transformToVW(15px);

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;


    input {
      width: transformToVW(100px);
      padding: transformToVW(5px) transformToVW(10px);

      font: inherit;
      color: inherit;

      background: #2D2F33;
      outline: none;
      border: transformToVW(1px) solid #575C66;

      border-radius: 0;

      text-align: center;

      &:focus {
        border: transformToVW(1px) solid $select-color;
      }
    }
  }

  &__add-btn {
    // display: block;
    // width: 100%;
    padding: transformToVW(5px) transformToVW(10px);

    border: none;
    outline: none;
    background: $select-color;
    cursor: pointer;

    font: inherit;
    color: inherit;
  }
}