$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.page-chooser {
  grid-column: 1/2;
  grid-row: 2/3;

  background: #313438;

  padding: 0 transformToVW(15px);

  display: flex;
  justify-content: space-between;
  align-items: center;
}